<template>
  <div class="bln">
    <div class='bln-inner'></div>
    <div class='bln-outer'></div>
    <div class='bln-content'>
      <slot></slot>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'BreathingLight'
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .bln {
    $width: 40px; // 
    $bgc: #7f8c8d; // 
    $breathHz: 3000ms; // 
    $inner-width: $width * .8; // 
    $box-shadow: lighten($bgc, .5); // 
    
    position: relative;
    width: $width;
    height: $width;
    line-height: $width;
    text-align: center;
    cursor: pointer;
    
    /*  */
    .bln-content {
      position: absolute;
      left: 0;
      top: 0;
      width: $width;
      height: $width;
      line-height: $width;
    }
    
    /*  */
    .bln-inner {
      width: $inner-width;
      height: $inner-width;
      top: ($width - $inner-width)/2;
      left: ($width - $inner-width)/2;
      background-color: $bgc;
      box-shadow: 0 0 2px rgba(0, 0, 0, .1);
      animation: ease-in-out zommLess $breathHz infinite alternate;
    }
    
    /*  */
    .bln-outer {
      width: $width;
      height: $width;
      top: 0;
      left: 0;
      background-color: lighten($bgc, 10%);
      animation: ease-in-out breath $breathHz infinite alternate;
    }
    
    .bln-inner, .bln-outer {
      border-radius: 50%;
      display: block;
      position: absolute;
      overflow: visible;
    }
    
    @keyframes breath {
      0% {
        opacity: .2;
        box-shadow: 0 0 2px rgba(0, 0, 0, .1); }
      100% {
        opacity: .6;
        box-shadow: 0 0 30px 2px $box-shadow; }
    }
    
    @keyframes zommLess {
      0% {
        transform: scale(1); }
      100% {
        transform: scale(.95); }
    }
  }
</style>



