/**
*  2018/7/31
* : QianYu
* : 
*/


<template>
  <div class='demo-box demo-box-hook'
       ref='demoBox'
       :data-clipboard-text='thisName'
       :style='{
            width: thisWidth ? thisWidth + "px" : "",
            height: thisWidth ? thisWidth + "px" : ""}'>
    <slot></slot>
    <div class='demo-no'>
      {{thisName}}
    </div>
  </div>
</template>

<script type='text/javascript'>
  import ClipboardMixin from 'mixins/clipboardMixin'
  
  export default {
    name: 'DemoBox',
    mixins: [ClipboardMixin],
    props: {
      thisWidth: {
        type: Number,
        default: 180
      },       // 
      thisName: {
        type: String,
        default: ''
      }        // 
    },
    mounted() {
      this._loadEleClipboard(this.$refs.demoBox)
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  @import '~styles/mixin.scss';
  
  .demo-box {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    cursor: pointer;
    
    .demo-no {
      visibility: hidden;
      opacity: 0;
      position: absolute;
      left: 0;
      top: 2px;
      color: $border-grey;
    }
    
    &:hover {
      .demo-no {
        transition: visibility .6s;
        transition-delay: 4s;
        visibility: visible;
        opacity: 1;
      }
    }
  }
</style>



