* : QianYu
* : HoverAnimation0201 Hover
* Codepen: https://codepen.io/SHERlocked93/pen/RqNwjw
*/


<template>
  <div class="ha-0201">Hover!</div>
</template>

<script type='text/javascript'>
  export default {
    name: 'HoverAnimation0201'
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  $base-color: rgba(66, 185, 131, .8); // 
  
  .ha-0201 {
    display: inline-block;
    position: relative;
    color: $base-color;
    line-height: 25px;
    
    &:after {
      content: '';
      position: absolute;
      width: 100%;
      transform: scaleX(0);
      height: 2px;
      bottom: 0;
      left: 0;
      background-color: $base-color;
      transform-origin: bottom right; // 
      transition: transform 0.25s ease-out;
    }
    
    &:hover::after {
      transform: scaleX(1);
      transform-origin: bottom left;   // 
    }
  }
</style>



