import Clipboard from 'clipboard'

export default {
  data() {
    return {
      _clipboard: null      // Clipboard.js
    }
  },
  methods: {
    /**
     *  Clipboard 
     * @param el cssDOMDOMlist
     */
    _loadEleClipboard(el) {
      this._clipboard = new Clipboard(this.$refs.demoBox)
      
      this._clipboard.on('success', e => this.$message({
        type: 'success',
        dangerouslyUseHTMLString: true,
        message: ` <strong>${this.thisName}</strong> `
      }))
      
      this._clipboard.on('error', err => console.error('', err))
    }
  },
  destroyed() {
    this._clipboard &&
    (this._clipboard = null)
  }
}



