/**
*  2018/7/25
* : SHERlocked93
* :  Demo 
* slot name: blackboard 
* slot name: description 
* slot name: realization 
*/


<template>
  <div class='demo-block'>
    <!--  -->
    <div class='demo-board'>
      <slot name='blackboard'></slot>
    </div>
    
    <!--  -->
    <div class='demo-play' v-if='isExpand' ref='demoReal'>
      <div class='demo-desc' v-if='$slots.description'>
        <slot name='description'></slot>
      </div>
      
      <div class='demo-real' v-if='$slots.realization'>
        <slot name='realization'></slot>
      </div>
    </div>
    
    <!--  -->
    <div class='demo-footer'
         @click='clickFooter'>
      <i :class='isExpand ? "el-icon-caret-top" : "el-icon-caret-bottom"'></i>
      <span class='show-real'>{{isExpand? '' : ''}}</span>
    </div>
  </div>
</template>

<script type='text/javascript'>
  import CodepenMixin from 'mixins/codepenMixin'
  
  export default {
    name: 'DemoBlock',
    mixins: [CodepenMixin],
    data() {
      return {
        isExpand: false     // 
      }
    },
    methods: {
      /**
       * 
       */
      clickFooter() {
        this.isExpand = !this.isExpand
        this.$nextTick(() =>
          this._reloadEmbed(this.$refs.demoReal)
        )
      }
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  @import "~styles/mixin.scss";
  
  .demo-block {
    border: 1px solid $light-grey;
    border-radius: $radius;
    transition: $comm-transition;
    margin: $comm-distance 0;
    
    &:hover {
      border: 1px solid $border-grey;
      /*box-shadow: 0 0 2px 0 $box-shadow;*/
    }
    
    /*  */
    .demo-board {
      display: flex;
      flex-flow: row;
      flex-wrap: wrap;
      border-bottom: $comm-border;
      .demo-box {
        padding: $comm-distance;
        /*border-right: $comm-border;*/
      }
    }
    
    /*  */
    .demo-play {
      padding: $comm-distance;
      border-bottom: $comm-border;
      .demo-desc {
        background-color: $light-grey;
        border-radius: $radius;
        padding: $comm-distance;
        margin-bottom: $comm-distance;
      }
      .demo-real {
      }
    }
    
    /*  */
    .demo-footer {
      text-align: center;
      line-height: 40px;
      cursor: pointer;
      color: $light;
      
      [class^='el-icon'] {
        margin-right: 10px;
        transform: translateX(20px);
      }
      
      .show-real {
        visibility: hidden;
        opacity: 0;
        transform: translateX(20px);
      }
      
      [class^='el-icon'], .show-real {
        transition: $comm-transition;
      }
      
      &:hover {
        color: $green;
        [class^='el-icon'] {
          transform: translateX(0px);
        }
        .show-real {
          display: inline-block;
          transform: translateX(0);
          opacity: 1;
          visibility: initial;
        }
      }
    }
  }
</style>



